.preloader {
  z-index: 10000;
  visibility: hidden;
  opacity: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(247, 249, 251, .408);
  justify-content: center;
  align-items: center;
  margin: 0;
  transition: opacity .4s ease-in-out, visibility 0s .4s;
  display: flex;
  position: fixed;
  top: 0;
}

.show-preloader {
  visibility: visible;
  opacity: 1;
}

.circle {
  height: 15px;
  width: 15px;
  background-color: #ff6b01;
  border-radius: 50%;
  animation: stretchdelay .7s ease-in-out infinite;
  display: inline-block;
}

.preloader .circle:nth-child(1) {
  animation-delay: -.6s;
}

.preloader .circle:nth-child(2) {
  animation-delay: -.5s;
}

.preloader .circle:nth-child(3) {
  animation-delay: -.4s;
}

.preloader .circle:nth-child(4) {
  animation-delay: -.3s;
}

@keyframes stretchdelay {
  0%, 40%, 100% {
    transform: translateY(-10px);
  }

  20% {
    transform: translateY(-20px);
  }
}

